<template>
  <div>
    <el-table
      :data="applyCANRecordList"
      style="width:100%">
      <el-table-column
        label=""
      >
        <template slot-scope="scope">
          <p class="">{{ scope.row.createdAt | subDate }}</p>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        prop="canReceiveAddress">
      </el-table-column>
      <el-table-column
        label=" (CAN)">
        <template slot-scope="scope">
          <p class="inCanClass" v-if="scope.row.inOrOut === 'in'"><span>+</span>{{ scope.row.amount }}</p>
          <p class="outCanClass" v-else><span>-</span>{{ scope.row.amount }}</p>
        </template>
      </el-table-column>
      <el-table-column
        label="">
        <template slot-scope="scope">
          <p class="theme-fontColor">{{ scope.row.status | applyCanStatus }}</p>
        </template>
      </el-table-column>
    </el-table></div>
</template>

<script>
  import axios from 'axios'
  import { UTIL } from '../../util/util'
  export default {
    data () {
      return {
        applyCANRecordList: []
      }
    },
    mounted () {
      this.applyRecordDataRequest()
    },
    methods: {
      // 
      applyRecordDataRequest () {
        let vm = this
        axios.get('/promo/authed/account/coins/extract/lists/1/5')
          .then(function (response) {
            vm.applyCANRecordList = UTIL.fliterNullArr(response.data)
          })
          .catch(function (error) {
            console.log(error)
          })
      }
    }
  }
</script>
<style scoped>
  .inCanClass {
    color:#52C41A
  }
  .outCanClass {
    color:#F5222D
  }
  .inCanClass span {
    margin-right: 4px;
  }
  .outCanClass span {
    margin-right: 4px;
  }
</style>



