<template>
<div>
  <el-button type="primary" class="mining-btn marginLeft-85 fontSize-16" @click="commitGetCan" ></el-button>
  <el-dialog
  :visible.sync="dialogVisible"
  :close-on-click-modal="false"
  >
    <template slot="title">
      <i class="el-icon-warning theme-fontColor"></i>
      <span></span>
    </template>
  <span class="fontSize-14 fontcolor-opocity-54"></span>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false"> </el-button>
    <el-button type="warning" @click="sureGetCANBI"> </el-button>
  </span>
</el-dialog>
</div>
</template>
<script>
  import axios from 'axios'
  export default {
    props: ['canReceiveAddress', 'amount'],
    data () {
      return {
        dialogVisible: false
      }
    },
    watch: {
      canReceiveAddress (newData) {
      },
      amount (newData) {
      }
    },
    methods: {
      commitGetCan () {
        if (this.canReceiveAddress === '' && this.amount === '') {
          return false
        } else {
          this.dialogVisible = true
        }
      },
      //can
      sureGetCANBI () {
        console.log(this.canReceiveAddress)
        let vm = this
        axios.post('/promo/authed/account/coins/extract',
          {
            canReceiveAddress: vm.canReceiveAddress,
            amount: vm.amount,
            minerfee: 2
          })
          .then(function (response) {
            if (response.data.isSuccess) {
              vm.$message({
                message: '',
                type: 'success'
              })
              vm.dialogVisible = false
              return
            }
            vm.dialogVisible = false
            vm.$message({
              message: response.data.reason,
              type: 'error'
            })
          })
          .catch(function (error) {
            console.log(error)
          })
      }
    }
  }
</script>
<style scoped>
  .el-dialog--small {
    width:433px;
    height: 192px;
  }
  .marginLeft-85 {
    margin-left: 85px;
  }
  .mining-btn {
    padding:11px 47px;
  }
</style>



